<template>
  <div>
    <!-- List Box Import Excel Result-->
    <b-modal id="error-list-modal" title="ErrorList" cancel-disabled ok-only ok-title="Close" centered>
      <DataTable :value="errorList" :scrollable="true" scrollHeight="200px">
        <Column field="row" header="Row" headerStyle="width: 80px"></Column>

        <Column field="error" header="Error"></Column>
      </DataTable>
    </b-modal>

    <!--Add User Modal -->
    <b-modal id="add-user-modal" title="" size="lg" @ok="handleAddUser" centered>
      <ValidationObserver ref="formadd">
        <b-form>
          <b-row>
            <b-col cols="12" md="6" class="mb-md-0 mb-2">
              <b-form-group id="input-group-2" label="(*):" label-for="email-input-add">
                <ValidationProvider name="email" rules="required|email" #default="{ errors }">
                  <b-form-input id="email-input-add" v-model="form.email" placeholder="" required />
                  <span class="input-error">{{ errors[0] }}</span>
                </ValidationProvider>
              </b-form-group>
            </b-col>
            <b-col cols="12" md="6" class="mb-md-0 mb-2">
              <b-form-group id="input-group-1" label="(*):" label-for="user-name-add">
                <!-- description="We'll never share your email with anyone else." -->
                <ValidationProvider name="fullname" rules="required" #default="{ errors }">
                  <b-form-input id="user-name-add" type="text" v-model="form.username" placeholder=""
                    required></b-form-input>
                  <span class="input-error">{{ errors[0] }}</span>
                </ValidationProvider>
              </b-form-group>
            </b-col>
          </b-row>
          <b-row>
            <b-col cols="12" md="6" class="mb-md-0 mb-2">
              <b-form-group id="label-phone" label="(*):" label-for="input-phone-add">
                <ValidationProvider name="phone" rules="required|phonenumber" #default="{ errors }">
                  <b-form-input id="input-phone-add" placeholder="" v-model="form.phone" required></b-form-input>
                  <span class="input-error">{{ errors[0] }}</span>
                </ValidationProvider>
              </b-form-group>
            </b-col>
            <b-col cols="12" md="6" class="mb-md-0 mb-2">
              <b-form-group id="label-company" label=":" label-for="input-company-add">
                <b-form-input id="input-company-add" v-model="form.company" placeholder=""></b-form-input>
              </b-form-group>
            </b-col>
          </b-row>
          <b-row>
            <b-col cols="12" md="6" class="mb-md-0 mb-2">
              <b-form-group id="label-department" label=":" label-for="input-department-add">
                <b-form-input id="input-department-add" v-model="form.department" placeholder=""></b-form-input>
              </b-form-group>
            </b-col>
            <b-col cols="12" md="6" class="mb-md-0 mb-2">
              <b-form-group id="label-position" label=":" label-for="input-position-add">
                <b-form-input id="input-position-add" v-model="form.position" placeholder=""></b-form-input>
              </b-form-group>
            </b-col>
          </b-row>
          <b-row>
            <b-col cols="12" md="6" class="mb-md-0 mb-2">
              <b-form-group label=":" label-for="input-address-add">
                <b-form-input id="input-address-add" v-model="form.address" placeholder=""></b-form-input>
              </b-form-group>
            </b-col>
            <b-col cols="12" md="6" class="mb-md-0 mb-2">
              <b-form-group id="label-password" label="(*):" label-for="input-password-add">
                <ValidationProvider name="password" rules="passwordRule" #default="{ errors }">
                  <b-form-input id="input-password-add" type="password" v-model="form.password"
                    placeholder=""></b-form-input>
                  <span class="input-error">{{ errors[0] }}</span>
                </ValidationProvider>
              </b-form-group>
            </b-col>
          </b-row>

          <b-row>
            <b-col cols="12" md="6" class="mb-md-0 mb-2">
              <b-form-group id="label-role" label=":" label-for="input-role">
                <b-form-select :options="roles" v-model="selectAddUserRole">
                </b-form-select>
              </b-form-group>
            </b-col>
            <b-col cols="12" md="6" class="mb-md-0 mb-2">
              <b-form-group id="label-license-type" label=":" label-for="input-label-license-type">
                <b-form-select :options="licenseTypes" v-model="selectedLicenseType">
                </b-form-select>
              </b-form-group>
            </b-col>
          </b-row>
        </b-form>
      </ValidationObserver>
      <template #modal-cancel>
        <b-button class="p-0"></b-button>
      </template>
    </b-modal>
    <!--Edit User Modal -->
    <b-modal id="edit-user-modal" title="" @ok="handleEditUser" size="lg" centered>
      <ValidationObserver ref="formedit">
        <b-form>
          <b-row>
            <b-col cols="12" md="6" class="mb-md-0 mb-2">
              <b-form-group id="input-group-2" label=":" label-for="email-input">
                <b-form-input id="email-input" v-model="edit.email" placeholder="Email" disabled></b-form-input>
              </b-form-group>
            </b-col>
            <b-col cols="12" md="6" class="mb-md-0 mb-2">
              <b-form-group id="input-group-1" label=":" label-for="user-name">
                <!-- description="We'll never share your email with anyone else." -->
                <ValidationProvider name="fullname" rules="required" #default="{ errors }">
                  <b-form-input id="user-name" type="text" v-model="edit.username" placeholder=""
                    required></b-form-input>
                  <span class="input-error">{{ errors[0] }}</span>
                </ValidationProvider>
              </b-form-group>
            </b-col>
          </b-row>
          <b-row>
            <b-col cols="12" md="6" class="mb-md-0 mb-2">
              <b-form-group id="label-phone" label=":" label-for="input-phone">
                <ValidationProvider name="phone" rules="required|phonenumber" #default="{ errors }">
                  <b-form-input id="input-phone" placeholder="" v-model="edit.phone" required></b-form-input>
                  <span class="input-error">{{ errors[0] }}</span>
                </ValidationProvider>
              </b-form-group>
            </b-col>
            <b-col cols="12" md="6" class="mb-md-0 mb-2">
              <b-form-group id="label-company" label=":" label-for="input-company">
                <b-form-input id="input-company" v-model="edit.company" placeholder=""></b-form-input>
              </b-form-group>
            </b-col>
          </b-row>
          <b-row>
            <b-col cols="12" md="6" class="mb-md-0 mb-2">
              <b-form-group id="label-department" label=":" label-for="input-department">
                <b-form-input id="input-department" v-model="edit.department" placeholder=""></b-form-input>
              </b-form-group>
            </b-col>
            <b-col cols="12" md="6" class="mb-md-0 mb-2">
              <b-form-group id="label-position" label=":" label-for="input-position">
                <b-form-input id="input-position" v-model="edit.position" placeholder=""></b-form-input>
              </b-form-group>
            </b-col>
          </b-row>
          <b-row>
            <b-col cols="12" md="6" class="mb-md-0 mb-2">
              <b-form-group id="label-address" label=":" label-for="input-address">
                <b-form-input id="input-address" v-model="edit.address" placeholder=""></b-form-input>
              </b-form-group>
            </b-col>
            <b-col cols="12" md="6" class="mb-md-0 mb-2">
              <b-form-group id="label-password" label=":" label-for="input-password">
                <ValidationProvider name="password" rules="passwordRule" #default="{ errors }">
                  <b-form-input id="input-password" type="password" v-model="edit.password"
                    placeholder=""></b-form-input>
                  <span class="input-error">{{ errors[0] }}</span>
                </ValidationProvider>
              </b-form-group>
            </b-col>
          </b-row>
          <b-row>
            <b-col cols="12" md="6" class="mb-md-0 mb-2">
              <b-form-group id="label-role" label=":" label-for="input-role">
                <b-form-select :options="roles" v-model="selectEditUserRole">
                </b-form-select>
              </b-form-group>
            </b-col>

            <b-col cols="12" md="6" class="mb-md-0 mb-2">
              <b-form-group id="label-active" label=":">
                <template>
                  <el-radio v-model="edit.isActive" label="1"></el-radio>
                  <el-radio v-model="edit.isActive" label="0"></el-radio>
                </template>
              </b-form-group>
            </b-col>
          </b-row>

          <b-row>
            <b-col cols="12" md="6" class="mb-md-0 mb-2">
              <b-form-group id="label-license-type" label=":" label-for="input-label-license-type">
                <b-form-select :options="licenseTypes" v-model="selectedLicenseType">
                </b-form-select>
              </b-form-group>
            </b-col>
          </b-row>
        </b-form>
      </ValidationObserver>
      <template #modal-cancel>
        <b-button class="p-0"></b-button>
      </template>
    </b-modal>
    <!--Import Excel Modal -->
    <b-modal id="excel-modal" title="" @ok="handleImportExcel" cancel-title="" centered>
      <b-form-file locale="ja" browse-text=""
        accept="application/vnd.openxmlformats-officedocument.spreadsheetml.sheet" v-model="fileExcel"
        placeholder="" ref="fileinput" class="mb-2"></b-form-file>
    </b-modal>

    <user-filter v-on:clickSearch="handleSearch" :filter="search" />
    <b-card no-body class="mb-0 p-1">
      <!-- <h1>List User</h1> -->
      <div class="d-flex justify-content-between mb-1">
        <b-button variant="primary" @click="addUser"></b-button>
        <b-button variant="primary" @click="openImportExcelModal"></b-button>
      </div>
      <b-table show-empty :sort-by.sync="sortBy" :items="items" :fields="fields" :current-page="currentPage"
        :per-page="0" :busy="isBusy" :responsive="true" outlined :tbody-tr-class="rowClass">
        <template #table-busy>
          <div class="text-center text-danger my-2">
            <b-spinner class="align-middle"></b-spinner>
            <strong>...</strong>
          </div>
        </template>
        <template #cell(index)="data">
          {{ data.index + 1 }}
        </template>

        <template #cell(isActive)="data">
          <b-form-checkbox v-model="data.item.isActive" name="check-button"
            @change="(e) => handleChangeIsActive(e, data)" switch>
            {{ data.item.isActive ? "" : "" }}
          </b-form-checkbox>
        </template>

        <template #cell(isSendEmail)="data">
          <b-form-checkbox v-model="data.item.isSendEmail" name="check-button"
            @change="(e) => handleChangeIsSendEmail(e, data)" switch>
            {{ data.item.isSendEmail ? "" : "" }}
          </b-form-checkbox>
        </template>

        <!-- Column: Actions -->
        <template #cell(action)="data">
          <b-dropdown variant="link" no-caret :right="$store.state.appConfig.isRTL">
            <template #button-content>
              <feather-icon icon="MoreVerticalIcon" size="16" class="align-middle text-body" />
            </template>

            <b-dropdown-item @click="openEditUser(data)">
              <feather-icon icon="EditIcon" />
              <span class="align-middle ml-50"></span>
            </b-dropdown-item>

            <b-dropdown-item @click="confirmDelete(data)">
              <feather-icon icon="TrashIcon" />
              <span class="align-middle ml-50"></span>
            </b-dropdown-item>
          </b-dropdown>
        </template>
      </b-table>
      <div class="mx-2 mt-2">
        <b-row>
          <b-col cols="12" sm="6" class="
              d-flex
              align-items-center
              justify-content-center justify-content-sm-start
            ">
            <!-- <span class="text-muted">Showing {{ dataMeta.from }} to {{ dataMeta.to }} of {{ dataMeta.of }} entries</span> -->
          </b-col>
          <!-- Pagination -->
          <b-col cols="12" sm="6" class="
              d-flex
              align-items-center
              justify-content-center justify-content-sm-end
            ">
            <b-pagination v-model="currentPage" :total-rows="totalUsers" :per-page="perPage" first-number last-number
              class="mt-1 mt-sm-0" prev-class="prev-item" next-class="next-item">
              <template #prev-text>
                <feather-icon icon="ChevronLeftIcon" size="18" />
              </template>
              <template #next-text>
                <feather-icon icon="ChevronRightIcon" size="18" />
              </template>
            </b-pagination>
          </b-col>
        </b-row>
      </div>
    </b-card>
  </div>
</template>
<script>
import {
  BCard,
  BRow,
  BForm,
  BCol,
  BFormInput,
  BButton,
  BTable,
  BMedia,
  BAvatar,
  BLink,
  BBadge,
  BDropdown,
  BDropdownItem,
  BPagination,
  BFormGroup,
  BSpinner,
  BFormCheckbox,
  BFormSelect,
  BFormFile,
} from "bootstrap-vue";

import Checkbox from "primevue/checkbox";
import UserFilter from "./UserFilter.vue";
import axios from "@axios";
import ToastificationContent from "@core/components/toastification/ToastificationContent.vue";
import { ValidationProvider, ValidationObserver, extend } from "vee-validate";
import { required, email } from "vee-validate/dist/rules";
import { AwardIcon } from "vue-feather-icons";
import DataTable from "primevue/datatable";
import Column from "primevue/column";
extend("required", {
  ...required,
  message: "",
});

extend("passwordlength", (value) => {
  if (value.length >= 8) return true;

  return "8";
});

extend("phonenumber", (value) => {
  let isPhoneValid = /^[\+]?[(]?[0-9]{3}[)]?[-\s\.]?[0-9]{3}[-\s\.]?[0-9]{4,6}$/im.test(
    value
  );
  if (isPhoneValid) return true;

  return "";
});

extend("passwordRule", (value) => {
  let isPassword = /^(?=.*[a-z])(?=.*[A-Z])(?=.*\d)(?=.*[@$!%*?&])[A-Za-z\d@$!%*?&]{8,}$/g.test(
    value
  );
  if (isPassword) return true;

  return "";
});

extend("email", {
  ...email,
  message: "",
});

export default {
  data() {
    return {
      errorList: [],
      windowWidth: window.innerWidth,
      fileExcel: null,
      sortBy: "email",
      selectAddUserRole: null,
      selectEditUserRole: null,
      roles: [],
      items: [],
      licenseTypes: [
        {
          value: 0,
          text: "",
        },
        {
          value: 1,
          text: "",
        }
      ],
      selectedLicenseType: null,
      isMounted: false,
      isAdd: true,
      search: {
        name: "",
        email: "",
        phone: "",
        company: "",
        department: "",
        address: "",
        position: "",
        start: "",
        end: "",
        isActive: "All",
      },
      form: {
        username: "",
        email: "",
        company: "",
        phone: "",
        password: "",
        department: "",
        position: "",
        address: "",
      },
      edit: {
        id: "",
        username: "",
        email: "",
        company: "",
        phone: "",
        password: "",
        department: "",
        position: "",
        address: "",
        isActive: "0",
      },
      fields: [
        {
          key: "index",
          label: "No.",
          // thStyle: { width: "40px", padding: "0.72rem 1rem 0.72rem" },
          thStyle: { width: "40px" },
        },
        {
          key: "code",
          label: " ",
          // thStyle: { width: "40px", padding: "0.72rem 1rem 0.72rem" },
          thStyle: { width: "40px" },
        },
        {
          key: "fullName",
          label: "",
          sortable: true,
          //  thStyle: {  padding: "0.72rem 1rem 0.72rem", "min-width": "200px"  },
          thStyle: { "min-width": "180px" },
        },
        {
          key: "email",
          label: "",
          sortable: true,
          //  thStyle: {  padding: "0.72rem 1rem 0.72rem", "min-width": "200px"  },
          thStyle: { "min-width": "180px" },
        },
        {
          key: "company",
          label: "",
          sortable: true,
          //  thStyle: {  padding: "0.72rem 1rem 0.72rem", "min-width": "200px"  },
          thStyle: { "min-width": "180px" },
        },
        // {
        //   key: "department",
        //   label: "",
        //              thStyle: {  padding: "0.72rem 0.72rem", "min-width": "200px"  },
        // },
        // {
        //   key: "position",
        //   label: "",
        //              thStyle: {  padding: "0.72rem 0.72rem", "min-width": "200px"  },
        // },
        // {
        //   key: "address",
        //   label: "",
        //    thStyle: {  padding: "0.72rem 0.72rem",  "min-width": "200px" },
        //    tdClass: 'addressClass'
        // },
        {
          key: "phone",
          label: "",
          // thStyle: { width: "140px", padding: "0.72rem 1rem 0.72rem"  },
          thStyle: { width: "140px" },
        },
        {
          key: "role",
          label: "",
          // thStyle: { width: "140px", padding: "0.72rem 1rem 0.72rem"  },
          thStyle: { "min-width": "120px" },
          // thStyle: { width: '140px' },
        },
        {
          key: "createAt",
          label: "",
          // thStyle: { width: "140px", padding: "0.72rem 1rem 0.72rem" },
          thStyle: { width: "140px" },
          sortable: true,
        },
        {
          key: "isActive",
          label: "",
          thStyle: { padding: "0.72rem 0.72rem", "min-width": "150px" },
          // thStyle: { width: "150px",padding: "0.72rem 1rem 0.72rem" },
          // thStyle: { width: '170px' },
        },
        {
          key: "isSendEmail",
          label: "",
          thStyle: { padding: "0.72rem 0.72rem", "min-width": "150px" },
          // thStyle: { width: "150px",padding: "0.72rem 1rem 0.72rem" },
          // thStyle: { width: '170px' },
        },
        {
          key: "action",
          label: "",
          // thStyle: { width: "120px", padding: "0.72rem 1rem 0.72rem" },
          thStyle: { width: "120px" },
        },
      ],
      currentPage: 1,
      totalUsers: 1,
      perPage: 10,
      totalItems: 1,
      filter: "",
      currentUserId: null,
      queryString: "",
      isBusy: true,
    };
  },
  components: {
    DataTable,
    Column,
    BForm,
    BFormFile,
    BFormSelect,
    BFormCheckbox,
    Checkbox,
    BButton,
    BTable,
    BPagination,
    BCard,
    BRow,
    BCol,
    BFormInput,
    BMedia,
    BAvatar,
    BLink,
    BBadge,
    BDropdown,
    BDropdownItem,
    BFormGroup,
    BSpinner,
    ValidationProvider,
    ValidationObserver,
    UserFilter,
  },
  created() {
    // window.addEventListener("resize", this.handleResize);
    // console.log(this.fields)
  },
  destroyed() {
    // window.removeEventListener("resize", this.handleResize);
  },
  methods: {
    setWidthColumns() { },
    handleResize(e) {
      this.windowWidth = window.innerWidth;
    },
    async handleImportExcel() {
      if (this.$refs.fileinput.files.length == 1) {
        try {
          var bodyFormData = new FormData();
          bodyFormData.append("file", this.$refs.fileinput.files[0]);
          let res = await axios({
            url: `CustomUser/import-users`,
            method: "POST",
            data: bodyFormData,
            headers: { "Content-Type": "multipart/form-data" },
          });
          if (res.data.succeeded) {
            let okCount = res.data.data.okCount;
            // if (okCount > 0)
            this.$toast({
              component: ToastificationContent,
              position: "top-right",
              props: {
                title: "Success",
                text: `${okCount}`,
                variant: "success",
              },
            });

            if (res.data.data.errorRows.length > 0) {
              this.errorList = res.data.data.errorRows;
              this.$bvModal.show("error-list-modal");
            }
            this.$bvModal.hide("modal-import");
            await this.getData();
          } else {
            this.$toast({
              component: ToastificationContent,
              position: "top-right",
              props: {
                title: "",
                text: "",
                variant: "danger",
              },
            });
          }
        } catch {
          this.$toast({
            component: ToastificationContent,
            position: "top-right",
            props: {
              title: "",
              text: "",
              variant: "danger",
            },
          });
        }
      } else {
        this.$toast({
          component: ToastificationContent,
          position: "top-right",
          props: {
            title: "Warning",
            text: "",
            variant: "warning",
          },
        });
      }
    },
    openImportExcelModal() {
      this.fileExcel = null;
      this.$bvModal.show("excel-modal");
    },
    async handleChangeIsActive(e, data) {
      let payload = {
        id: data.item.id,
        name: data.item.fullName,
        phone: data.item.phone,
        company: data.item.company,
        isActive: data.item.isActive,
      };
      try {
        let res = await axios.put("CustomUser/edit-user", payload);
        if (res.data.succeeded) {
          this.$toast({
            component: ToastificationContent,
            position: "top-right",
            props: {
              title: "",
              text: ``,
              variant: "success",
            },
          });
        } else {
          this.$toast({
            component: ToastificationContent,
            position: "top-right",
            props: {
              title: "",
              text: "",
              variant: "danger",
            },
          });
        }
      } catch (e) {
        this.$toast({
          component: ToastificationContent,
          position: "top-right",
          props: {
            title: "",
            text: "",
            variant: "danger",
          },
        });
      }
      this.isBusy = false;
    },

    async handleChangeIsSendEmail(e, data) {
      let payload = {
        id: data.item.id,
        name: data.item.fullName,
        phone: data.item.phone,
        company: data.item.company,
        isActive: data.item.isActive,
        isSendEmail: data.item.isSendEmail,
      };
      try {
        let res = await axios.put("CustomUser/edit-user", payload);
        if (res.data.succeeded) {
          this.$toast({
            component: ToastificationContent,
            position: "top-right",
            props: {
              title: "",
              text: ``,
              variant: "success",
            },
          });
        } else {
          this.$toast({
            component: ToastificationContent,
            position: "top-right",
            props: {
              title: "",
              text: "",
              variant: "danger",
            },
          });
        }
      } catch (e) {
        this.$toast({
          component: ToastificationContent,
          position: "top-right",
          props: {
            title: "",
            text: "",
            variant: "danger",
          },
        });
      }
      this.isBusy = false;
    },

    async handleSearch() {
      this.isBusy = true;
      await this.getData();
      this.isBusy = false;
    },
    objectToQueryString(obj) {
      var str = [];
      for (var p in obj)
        if (obj.hasOwnProperty(p)) {
          str.push(encodeURIComponent(p) + "=" + encodeURIComponent(obj[p]));
        }
      return str.join("&");
    },
    addUser() {
      this.selectedLicenseType = this.licenseTypes[0].value;
      this.$bvModal.show("add-user-modal");
    },
    openEditUser(dataRow) {
      let data = { ...dataRow };
      console.log(data);
      this.edit.id = data.item.id;
      this.edit.email = data.item.email;
      this.edit.company = data.item.company;
      this.edit.username = data.item.fullName;
      this.edit.phone = data.item.phone;
      this.edit.department = data.item.department;
      this.edit.position = data.item.position;
      this.edit.address = data.item.address;
      this.edit.password = "";
      this.edit.isActive = data.item.isActive ? "1" : "0";
      this.edit.isSendEmail = data.item.isSendEmail;
      this.selectEditUserRole = data.item.roleId;
      this.selectedLicenseType = data.item.licenseType;
      this.$bvModal.show("edit-user-modal");
    },
    async handleAddUser(bvModalEvt) {
      bvModalEvt.preventDefault();
      let isFormValid = await this.$refs.formadd.validate();
      if (!isFormValid) {
        return;
      }
      this.$bvModal.hide("add-user-modal");
      this.isBusy = true;
      let payload = {
        name: this.form.username,
        email: this.form.email,
        phone: this.form.phone,
        password: this.form.password,
        company: this.form.company,
        roleId: this.selectAddUserRole,
        position: this.form.position,
        department: this.form.department,
        address: this.form.address,
        licenseType: this.selectedLicenseType
      };
      try {
        let res = await axios.post("CustomUser/register", payload);
        if (res.data.succeeded) {
          this.$toast({
            component: ToastificationContent,
            position: "top-right",
            props: {
              title: "",
              text: ``,
              variant: "success",
            },
          });
          this.getData();
        } else {
          this.$toast({
            component: ToastificationContent,
            position: "top-right",
            props: {
              title: "Warning",
              text: res.data.message,
              variant: "danger",
            },
          });
        }
      } catch (e) {
        this.$toast({
          component: ToastificationContent,
          position: "top-right",
          props: {
            title: "",
            text: e.response.data.message,
            variant: "danger",
          },
        });
      }
      this.form.username = "";
      this.form.email = "";
      this.form.company = "";
      this.form.password = "";
      this.form.phone = "";
      this.form.position = "";
      this.form.department = "";
      this.form.address = "";

      this.isBusy = false;
    },
    async getData() {
      try {
        let activeFilter = "";
        if (this.search.isActive == "True") {
          activeFilter = true;
        }
        if (this.search.isActive == "False") {
          activeFilter = false;
        }

        let payload = { ...this.search };
        payload.isActive = activeFilter;

        let queryString = this.objectToQueryString(payload);

        let res = await axios.get(
          `CustomUser/?pageindex=${this.currentPage}&${queryString}`
        );
        this.items = res.data.data.items;
        this.totalUsers = res.data.data.totalCount;
      } catch { }
    },
    async handleFilter() {
      this.isBusy = true;
      await this.getData();
      this.isBusy = false;
    },
    async handleEditUser(bvModalEvt) {
      bvModalEvt.preventDefault();
      let isFormValid = await this.$refs.formedit.validate();
      if (!isFormValid) {
        return;
      }
      this.$bvModal.hide("edit-user-modal");

      this.isBusy = true;
      let payload = {
        id: this.edit.id,
        name: this.edit.username,
        phone: this.edit.phone,
        password: this.edit.password,
        company: this.edit.company,
        isActive: this.edit.isActive == 1 ? true : false,
        isSendEmail: this.edit.isSendEmail,
        roleId: this.selectEditUserRole,
        address: this.edit.address,
        department: this.edit.department,
        position: this.edit.position,
        licenseType: this.selectedLicenseType
      };
      try {
        let res = await axios.put("CustomUser/edit-user", payload);
        if (res.data.succeeded) {
          this.$toast({
            component: ToastificationContent,
            position: "top-right",
            props: {
              title: "Success",
              text: ``,
              variant: "success",
            },
          });
        } else {
          this.$toast({
            component: ToastificationContent,
            position: "top-right",
            props: {
              title: "Failed",
              text: "",
              variant: "danger",
            },
          });
        }
      } catch (e) {
        this.$toast({
          component: ToastificationContent,
          position: "top-right",
          props: {
            title: "Failed",
            text: e.response.data.message,
            variant: "danger",
          },
        });
      }
      await this.getData();
      this.isBusy = false;
    },
    async getRoles() {
      let res = await axios.get("CustomUser/all-role");
      let roleData = res.data.data;
      if (roleData) {
        this.roles = roleData.map(function (item) {
          return {
            value: item.id,
            text: item.name,
          };
        });
      }
    },
    async confirmDelete(data) {
      this.$confirm("", "", {
        confirmButtonText: "OK",
        cancelButtonText: "",
        type: "warning",
      })
        .then(() => {
          axios.delete(`users/?id=${data.item.id}`).then(async (response) => {
            if (response.data.succeeded) {
              await this.getData();
              this.$toast({
                component: ToastificationContent,
                position: "top-right",
                props: {
                  title: "",
                  text: ``,
                  variant: "success",
                },
              });
            } else {
              this.$toast({
                component: ToastificationContent,
                position: "top-right",
                props: {
                  title: "",
                  text: "",
                  variant: "danger",
                },
              });
            }
          });
        })
        .catch(() => {
          this.$toast({
            component: ToastificationContent,
            position: "top-right",
            props: {
              title: "Canceled",
              text: "",
              variant: "info",
              solid: true,
            },
          });
        });
    },
    rowClass(item, type) {
      if (!item || type !== 'row') return
      if (item.licenseType === 1) return 'table-success'
    }
  },
  async mounted() {
    this.isBusy = true;
    await this.getData();
    this.isBusy = false;
    this.isMounted = true;
    this.getRoles();
  },
  watch: {
    currentPage: {
      handler: async function (value) {
        this.isBusy = true;
        await this.getData();
        this.isBusy = false;
      },
    },
  },
};
</script>

<style>
#btn-search {
  padding: 0.785rem 1.4rem !important;
  border-color: #ccc;
  border-bottom-left-radius: 0;
  border-top-left-radius: 0;
}

#input-search {
  border-top-right-radius: 0;
  border-bottom-right-radius: 0;
  border-right: none;
}

.input-error {
  color: #f22435;
  display: block;
  margin-top: 5px;
  margin-left: 2px;
}

.addressClass {
  word-break: break-all;
}

.isActiveClass {
  width: 300px;
}
</style>
